import React, { useState } from 'react'
import { Avatar, Button, Menu, MenuItem } from '@material-ui/core'
import { Edit, DeleteOutline, MoreHoriz } from '@material-ui/icons'
import PostEdit from './PostEdit'
import axios from '../../axios'
import { useStateValue } from '../../Store/StateProvider'
import { actionTypes } from '../../Store/reducers/Reducer'

const PostMenu = ({ id, username, email, message, imgName, avatar, timestamp }) => {
  const [{ user }, dispatch] = useStateValue()
  const [anchorEl, setAnchorEl] = useState(null) // elements of the modal
  const [showEditBox, setShowEditBox] = useState(false)
  
  // modal opens
  const handleClick = event => { 
    setAnchorEl(event.currentTarget)
  }
  // modal close
  const handleClose = () => {
    setAnchorEl(null);
  }

  // delete post
  const handleDelete = () => {
    //console.log('Post.js handleDelete retrievePost,id', id)
    if( user.email === email ) {
      // delete it from the DB
      axios.delete( 'posts/delete/' + id )
        .then( res => {
          console.log( res.data )

          if(res.data.value === true) {
            // delete it from the DOM
            dispatch({
              type: actionTypes.DELETE_POST,
              deletePostId: res.data.id 
            })

            return true
          }
        })
        .catch( err => console.log( 'error', err ) )
    } else {
      alert('Unauthorized!')
      return false
    } 
    // close the card options
    setAnchorEl(null) 
  }

  // open the edit box
  const handleEdit = e => {
    e.preventDefault()
    // close modal
    setAnchorEl(null)
    // toggle edit post box
    setShowEditBox(!showEditBox) 
  }

  return (
    <div>
      {/* Options Menu */}
      <Menu
        id="simple-menu2"
        anchorEl={ anchorEl }
        keepMounted
        open={ Boolean(anchorEl) }
        onClose={ handleClose } 
      >
        <div onClick={ handleDelete }>
          <MenuItem>
            <DeleteOutline/>
            <div className='post__Menu__options'>Delete</div>
          </MenuItem>
        </div>

        <div onClick={ handleEdit }>
          <MenuItem >
            <Edit />
            <div className='post__Menu__options'>Edit</div>
          </MenuItem>
        </div>
      </Menu>

      {/* Post Header */}
      <div className="post__top">
        <div className="post__top__left">
          <Avatar src={ avatar } className='post__avatar' />
          <div className="post__topInfo">
          <h3>{ username }</h3>
            <p>{ new Date(parseInt(timestamp)).toUTCString() }</p>
          </div>
        </div>
        <div >
          <Button aria-controls="simple-menu2" aria-haspopup="true" onClick={handleClick}>
            <MoreHoriz className="post__top__right"/>
          </Button>
        </div>
      </div>

      {/* Edit Post */}
      <PostEdit
        id={ id }
        username={ username }
        email={ email }
        avatar={ user.photoURL }
        message={ message }
        imgName={ imgName }
        timestamp={ timestamp }
        showEditBox={ showEditBox } 
        setShowEditBox={ setShowEditBox }
      />
    </div>
  )
}

export default PostMenu
